}

header ul li a {
    color: #fff;
    font-weight: 500;
    transition: 0.5s;
}

header ul li a:hover{
    color: #1eb2a6;
}

.protect-together {
    background-color: #1eb2a6;
    padding: 30px 70px;
    color: #fff;
    -webkit-clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
}

.toggle {
    display: none;
}

.logo_sub{
    /* color: #3c3e41; */
    color:#fff;
}

.typewriter {
    color:#1eb2a6
}

@media screen and (max-width: 768px){
    .protect-together {
        -webkit-clip-path: none;
                clip-path: none;
    }

    header {
        margin: 0;
        background-color: #1eb2a6;
        position: relative;
    }

    header ul.flexSB {
        display: none;
    }

    header ul li {
        margin-bottom: 20px;
    }

    .toggle {
        display: block;
        background: none;
        color: #3c3e41;
        font-size: 30px;
        position: absolute;
        right: 50px;
        top: 20px;
    }

    .mobile-nav {
        position: absolute;
        top: 7vh;
        left: 0;
        width: 100%;
        background-color: #1eb2a6;
    }
}
